.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.container {
  position: relative;
  display: inline-flex;
  font-size: var(--input-font-size);
  min-inline-size: var(--input-min-width);
  max-inline-size: 100%;
  width: 10.5rem;
}
.container:not(:has(.hide-border)):not(:has(.readonly))::after {
  position: absolute;
  content: "";
  block-size: var(--input-bottom-bd-block-size, 1px);
  background-color: var(--input-active-bd, var(--input-focus-bd));
  inline-size: 0;
  inset-block-end: var(--input-bottom-bd-inset-block-end, 0);
  inset-inline-start: 0;
  transition: all calc(var(--input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--input-bottom-bd-anim-tm-ease-out);
}
.container.focused:not(:has(.hide-border)):not(:has(.readonly))::after {
  inline-size: 100%;
  transition: all calc(var(--input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--input-bottom-bd-anim-tm-ease-in);
}

.input {
  width: 100%;
  min-block-size: var(--input-min-height);
  padding: var(--input-pd);
  flex-grow: 1;
  background: var(--input-bg);
  color: var(--input-fg);
  cursor: text;
  border-width: var(--input-bd-width);
  border-style: solid;
  border-color: var(--input-bd);
  box-sizing: border-box;
  line-height: var(--input-line-height);
  font-family: var(--input-fontFamily, inherit);
  font-size: var(--input-fontSize, inherit);
  font-style: var(--input-fontStyle, inherit);
  font-weight: var(--input-fontWeight, inherit);
  text-decoration: var(--input-textDecoration, inherit);
}
.input:focus {
  outline: var(--input-focus-outline);
  outline-offset: var(--input-focus-outline-offset);
}
.input:focus:not(.has-input-icon):not(.readonly):not(.disabled) {
  border-color: var(--input-focus-bd);
  background-color: var(--input-focus-bg);
  color: var(--input-focus-fg);
}
.input:hover:not(.readonly):not(.disabled) {
  border-color: var(--input-hover-bd);
  background-color: var(--input-hover-bg);
  color: var(--input-hover-fg);
}
.input:invalid {
  box-shadow: none;
}
.input::placeholder {
  color: var(--input-placeholder-fg);
  /* Override default opacity value in FireFox (** https://developer.mozilla.org/en-US/docs/Web/CSS/::placeholder#opaque_text **) */
  opacity: 1;
}
.input::selection {
  color: var(--input-selection-fg);
  background-color: var(--input-selection-bg);
}
.input::-ms-clear {
  display: none;
}

.hide-border {
  border: none;
}

.input-icon {
  position: absolute;
  inset-block-start: 1px;
  inset-block-end: 1px;
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--input-spacing-xs);
  cursor: default;
}

.has-status-and-clear .input {
  padding-inline-end: calc(2 * (1.275rem + 2 * var(--input-spacing-xs)));
}
.has-status-and-clear .input-icon {
  margin-inline-end: calc(1.275rem + var(--input-spacing-xs) + 2 * var(--input-spacing-xs));
}

.has-status-xor-clear .input {
  padding-inline-end: calc(1.275rem + 2 * var(--input-spacing-xs));
}

.disabled {
  opacity: 1;
  cursor: default;
  color: var(--input-disabled-fg);
  border-color: var(--input-disabled-bd);
  background-color: var(--input-disabled-bg);
}
.disabled::placeholder {
  color: var(--input-disabled-fg);
}

.readonly:not(.has-input-icon):not(.composite) {
  background-color: var(--input-noneditable-bg);
  border-width: 0;
  color: var(--input-readonly-fg);
  text-overflow: ellipsis;
  overflow: hidden;
}
.readonly:not(.has-input-icon):not(.composite):focus {
  outline-offset: calc(var(--focus-width) * -1);
}
.readonly:not(.has-input-icon):not(.composite).disabled {
  color: var(--input-disabled-fg);
}

.composite:not(.has-input-icon), .composite:not(.has-input-icon):hover {
  border-color: transparent;
}
.composite:not(.has-input-icon):focus, .composite:not(.has-input-icon).readonly:focus {
  border-color: transparent;
}
.composite.disabled {
  color: var(--input-disabled-fg);
  background-color: transparent;
}
.composite.readonly {
  background-color: transparent;
}
.composite.readonly:not(.disabled) {
  cursor: var(--input-composite-cursor-style);
}
.composite:focus {
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.rtl {
  text-align: right;
}

.inplace-edit {
  padding: var(--input-pd);
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  box-sizing: border-box;
  line-height: normal;
  width: 100%;
  min-height: var(--input-min-height);
  color: var(--input-inplace-edit-text-fg);
}
.inplace-edit:hover {
  border-color: var(--input-hover-bd);
}
.inplace-edit.disabled {
  color: var(--input-disabled-fg);
}

.composite-input-icon {
  flex-shrink: 0;
  display: inline-flex;
  padding: var(--input-icon-padding);
  align-items: center;
}
.composite-input-icon.non-editable {
  background-color: var(--input-noneditable-bg);
}
.composite-input-icon.readonly {
  background-color: var(--input-readonly-bg);
}

.clear-button {
  position: absolute;
  inset-inline-end: 0;
  inset-block: 0 1px;
}

