@use 'reactDnd/dndstyles';

.tile {
  &.selected,
  &:hover {
    .checkbox {
      display: inline-block;
    }
  }

  &.selectable:hover {
    cursor: pointer;
  }
}

// Tile bar with Checkbox and State Icons
.bar {
  display: flex;
  z-index: 10;
  align-items: flex-start;
  position: absolute;
  width: 100%;
}

.checkbox {
  display: none;
  margin-block-start: 3px;
  margin-inline-start: 3px;
}

// Thumbnail + Content
.content-container {
  flex-basis: 100%;
  flex-grow: 1;
  overflow: auto;
  position: relative;
  z-index: 5;

  .thumbnail-container {
    display: flex;
    align-items: center;
    justify-content: center;

    &:focus {
      outline-offset: calc(var(--focus-width) * -1);
      outline: var(--focus-light-bg);
    }

    // SVG thumbnails
    > span {
      block-size: 100%;
      inline-size: 100%;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;

      > svg {
        max-block-size: 4rem;
        max-inline-size: 4rem;
      }
    }
  }

  // Vertical Orientation
  &.vertical {
    .thumbnail-container {
      justify-content: center;

      // Image thumbnails
      &:has(img) {
        aspect-ratio: 16 / 9;
      }

      > img {
        object-fit: cover;
        block-size: 100%;
        inline-size: 100%;
        max-inline-size: 100%;
      }

      // SVG thumbnails
      > span {
        padding: var(--tile-content-pd);
        box-sizing: border-box;
      }
    }

    // Tile Content
    .content {
      padding: var(--tile-content-pd);
    }
  }

  // Horizontal Orientation
  &.horizontal {
    display: flex;
    align-items: center;
    padding: var(--tile-content-pd);
    gap: var(--tile-content-pd);

    .thumbnail-container {
      flex-shrink: 0;
    }

    // Tile Content
    .content {
      flex-grow: 1;
    }
  }
}

.footer {
  display: flex;
  align-items: flex-start;
}

.object-marker {
  background-color: var(--tile-object-marker-bg);
  min-block-size: 0;
  margin-block-start: 2px;
  margin-inline-end: 2px;

  > span :global(.iconColor) {
    --icon-default-fg: var(--tile-object-marker-icon-fg);
  }
}

.object-status {
  margin-block-start: 1px;
  margin-inline-start: 1px;
}

.draggable {
  @extend %draggable;
}

.dragged-item {
  @extend %draggedItem;
}

.drag-preview {
  margin: 0;

  & .checkbox {
    // the checkbox should always be visible in the drag preview
    display: inline-block;
  }
}

// Drop line
@include dndstyles.DropLine($verticalBar: true);

.drop-container::after {
  top: 0;
}

.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}

// Override form labels spaces
.metadata-table-form .metadata-table-row {
  > div + div {
    align-self: center;
    padding: 0;
  }
}
