.default-color-picker-container {
  height: var(--color-picker-height); //  this is taken as we need to keep height unchanged
  padding: var(--color-picker-pd);
}

.tabbar-container {
  width: 100%;
  box-sizing: border-box;
}

.invisible-label {
  top: 0;
  left: 0;
  height: 0;
  position: absolute;
  visibility: hidden;
  pointer-events: none;
}
