@use 'utils/offscreen';
@use 'utils/input';

.container {
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;
  --ns-button-width: calc(
    1px * 2 + var(--numeric-stepper-button-icon-width) +
      var(--numeric-stepper-button-inline-padding) * 2
  );

  & > .text-field {
    flex-grow: 1;
    flex-shrink: 1;
    font-size: var(--input-font-size);

    & > div:has(svg) {
      inset-inline-end: calc(var(--ns-button-width) * 2 + 1px);
    }

    &:focus-within::before {
      border-color: var(--numeric-stepper-input-focus-bd);
      background-color: transparent;
      outline: var(--numeric-stepper-input-focus-outline);
      outline-offset: var(--numeric-stepper-input-focus-outline-offset);
      position: absolute;
      content: '';
      inset: 0;
      z-index: 1;
      pointer-events: none;

      &:not(.has-input-icon):not(.readonly):not(.disabled) {
        border-color: var(--input-focus-bd);
      }
    }

    &::after {
      z-index: 1;
    }

    & > .non-editable-text-field {
      width: 100%;
      min-block-size: var(--input-min-height);
      padding: var(--input-pd);
      flex-grow: 1;
      background: var(--input-bg);
      color: var(--input-fg);
      cursor: text;
      box-sizing: border-box;
      line-height: var(--input-line-height);
      font-family: var(--input-fontFamily, inherit);
      font-size: var(--input-fontSize, inherit);
      font-style: var(--input-fontStyle, inherit);
      font-weight: var(--input-fontWeight, inherit);
      text-decoration: var(--input-textDecoration, inherit);
      height: 100%;
      padding-inline-start: var(--numeric-stepper-input-inline-start-pd);
      padding-inline-end: calc(var(--numeric-stepper-input-inline-pd) + var(--ns-button-width) * 2);
      position: relative;
      border-width: var(--numeric-stepper-bd-width);
      border-style: solid;
      border-color: var(--numeric-stepper-input-bd);

      // border animation
      &:not(.disabled):after {
        @include input.AnimFocusBorder($tokenPrefix: 'numeric-stepper-input');
        z-index: 1;
      }

      &:not(.disabled):focus-within:after {
        @include input.AnimFocusBorderActive($tokenPrefix: 'numeric-stepper-input');
      }

      &.disabled {
        opacity: 1;
        cursor: default;
        color: var(--input-disabled-fg);
        border-color: var(--input-disabled-bd);
      }

      &.rtl {
        text-align: right;
      }
    }

    & > input {
      height: 100%;
      padding-inline-end: calc(var(--numeric-stepper-input-inline-pd) + var(--ns-button-width) * 2);
    }
  }

  & .has-status > input,
  & > .non-editable-text-field {
    --ns-input-status-icon-padding: calc(0.875rem + var(--input-spacing-xs) * 2);
    padding-inline-end: calc(var(--ns-input-status-icon-padding) + var(--ns-button-width) * 2);
  }

  & .button {
    position: absolute;
    min-block-size: 0;

    &.up-button {
      inset-inline-end: 0px;
      inset-block: 0 1px;
    }

    &.down-button {
      inset-inline-end: var(--ns-button-width);
      inset-block: 0 1px;
    }
  }
}

.input-icon {
  position: absolute;
  inset-block-start: 1px;
  inset-block-end: 1px;
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--input-spacing-xs);
  cursor: default;
}

.offscreen {
  @extend %offscreen;
}
