.current-new-swatch-container {
  width: 100%;
  justify-content: center;
  align-items: center;
}
.current-new-swatch-container .current-multi-color {
  width: var(--color-indicator-automatic-swatch-width);
  cursor: default;
}
.current-new-swatch-container .current-multi-color:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.current-new-swatch-wrapper {
  border: 1px solid var(--color-indicator-swatch-bd);
  margin-inline: var(--color-indicator-spacing-xs);
}

.swatch {
  box-sizing: border-box;
  width: 1.5rem;
  height: 1rem;
}
.swatch:focus {
  outline: var(--focus-light-bg);
  outline-offset: var(--color-indicator-focus-outline-offset);
  z-index: 1;
}

.no-color {
  background: linear-gradient(328deg, transparent 46%, var(--color-indicator-no-color-mark) 50%, var(--color-indicator-no-color-mark) 50%, transparent 54%);
}

