@use 'utils/offscreen';
@use 'reactDnd/dndstyles';
@use 'utils/input';
$min-height: 2.125rem;

.outer-container {
  width: 100%;
  min-width: 0;
}

.clear-all-button {
  max-height: $min-height;
}

.token-input-container {
  max-width: 100%;
  background-color: var(--token-input-bg);
  box-sizing: border-box;
  border-color: var(--token-input-bd-color);
  border-width: var(--token-input-bd-width);
  border-style: solid;
  position: relative;

  &:hover {
    background-color: var(--token-input-hover-bg);
    border-color: var(--token-input-hover-bd);
  }

  &:focus-within {
    background-color: var(--token-input-active-bg);
    border-color: var(--token-input-active-bd);
  }

  &:not(.disabled) {
    &:after {
      @include input.AnimFocusBorder($tokenPrefix: 'token-input');
    }

    &:has(.focus-visible):after {
      @include input.AnimFocusBorderActive($tokenPrefix: 'token-input');
    }
  }

  &.read-only {
    border-color: transparent;

    &:hover {
      background-color: var(--token-input-bg);
    }
  }
}

.container {
  width: 100%;
  min-width: 0;
  position: relative;
  display: flex;
  align-items: center;
  min-height: $min-height; // so that token input does not grow when the first token is added

  &.has-status-icon {
    padding-inline-end: 1.25rem;
  }
}

.input-field-container {
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  min-width: 8rem;
}

.input-field {
  width: 100%;
  height: 100%;

  & > input {
    background: transparent;

    &:hover {
      background: transparent;
    }

    &:focus {
      outline: var(--input-focus-outline);
      background: transparent;
    }
  }
}

.input-icon {
  position: absolute;
  height: 1.25rem;
  inset-block-start: var(--token-input-icon-position-top);
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--token-input-spacing-xs);
  cursor: default;
}

.offscreen {
  @extend %offscreen;
}

.button {
  width: $min-height;
  height: $min-height;
}

.tokenizer {
  width: 100%;
}

.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}
