.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.drag-over {
  background: var(--dnd-drop-zone-bg);
}

.drag-over {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

.outer-container {
  width: 100%;
  min-width: 0;
}

.clear-all-button {
  max-height: 2.125rem;
}

.token-input-container {
  max-width: 100%;
  background-color: var(--token-input-bg);
  box-sizing: border-box;
  border-color: var(--token-input-bd-color);
  border-width: var(--token-input-bd-width);
  border-style: solid;
  position: relative;
}
.token-input-container:hover {
  background-color: var(--token-input-hover-bg);
  border-color: var(--token-input-hover-bd);
}
.token-input-container:focus-within {
  background-color: var(--token-input-active-bg);
  border-color: var(--token-input-active-bd);
}
.token-input-container:not(.disabled):after {
  position: absolute;
  content: "";
  block-size: var(--token-input-bottom-bd-block-size, 1px);
  background-color: var(--token-input-active-bd, var(--token-input-focus-bd));
  inline-size: 0;
  inset-block-end: var(--token-input-bottom-bd-inset-block-end, 0);
  inset-inline-start: 0;
  transition: all calc(var(--token-input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--token-input-bottom-bd-anim-tm-ease-out);
}
.token-input-container:not(.disabled):has(.focus-visible):after {
  inline-size: 100%;
  transition: all calc(var(--token-input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--token-input-bottom-bd-anim-tm-ease-in);
}
.token-input-container.read-only {
  border-color: transparent;
}
.token-input-container.read-only:hover {
  background-color: var(--token-input-bg);
}

.container {
  width: 100%;
  min-width: 0;
  position: relative;
  display: flex;
  align-items: center;
  min-height: 2.125rem;
}
.container.has-status-icon {
  padding-inline-end: 1.25rem;
}

.input-field-container {
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  min-width: 8rem;
}

.input-field {
  width: 100%;
  height: 100%;
}
.input-field > input {
  background: transparent;
}
.input-field > input:hover {
  background: transparent;
}
.input-field > input:focus {
  outline: var(--input-focus-outline);
  background: transparent;
}

.input-icon {
  position: absolute;
  height: 1.25rem;
  inset-block-start: var(--token-input-icon-position-top);
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--token-input-spacing-xs);
  cursor: default;
}

.button {
  width: 2.125rem;
  height: 2.125rem;
}

.tokenizer {
  width: 100%;
}

