.object-marker {
  display: inline-flex;
  max-width: 100%;
  align-items: center;
  min-height: 1.875rem;
}
.object-marker:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.object-marker-link {
  cursor: pointer;
}

.disabled {
  cursor: default;
}

.icon {
  flex-shrink: 0;
  padding: var(--object-marker-spacing-xxs);
}
.icon :global(.iconColor) {
  --icon-default-fg: var(--object-marker-icon-fg);
}

.animatedInProgress {
  z-index: 1;
}

