.form {
  background-color: var(--searchfield-bg);
  border-color: var(--searchfield-bd);
}
.form:hover {
  background-color: var(--searchfield-hover-bg);
  border-color: var(--searchfield-hover-bd);
}
.disabled.form {
  background-color: var(--searchfield-disabled-bg);
  border-color: var(--searchfield-disabled-bd);
}

.search-input > input {
  background: transparent;
  color: var(--searchfield-fg);
}
.search-input > input:hover {
  background: transparent;
  color: var(--searchfield-hover-fg);
}
.search-input > input:focus {
  color: var(--searchfield-active-fg);
  outline: none;
}
.disabled .search-input > input {
  background-color: transparent;
  color: var(--searchfield-disabled-fg);
}

.form {
  display: inline-flex;
  border-width: var(--searchfield-bd-width);
  border-style: solid;
  border-color: var(--searchfield-bd);
  box-sizing: border-box;
  width: 15rem;
  min-width: 6rem;
  min-height: 1.75rem;
  max-width: 100%;
  position: relative;
}
.form:not(.disabled):after {
  position: absolute;
  content: "";
  block-size: var(--searchfield-bottom-bd-block-size, 1px);
  background-color: var(--searchfield-active-bd, var(--searchfield-focus-bd));
  inline-size: 0;
  inset-block-end: var(--searchfield-bottom-bd-inset-block-end, 0);
  inset-inline-start: 0;
  transition: all calc(var(--searchfield-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--searchfield-bottom-bd-anim-tm-ease-out);
}
.form.active {
  outline: var(--searchfield-focus-outline);
  outline-offset: var(--searchfield-focus-outline-offset);
}
.form.active:not(.disabled) {
  border-color: var(--searchfield-active-bd);
  background-color: var(--searchfield-active-bg);
}
.form.active:not(.disabled):after {
  inline-size: 100%;
  transition: all calc(var(--searchfield-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--searchfield-bottom-bd-anim-tm-ease-in);
}

.search-input {
  flex-grow: 1;
  min-width: 0;
}
.search-input > input {
  min-height: 0;
  width: 100%;
}

.reset-button {
  flex-shrink: 0;
}

.reset-button-search {
  padding: 0;
}

.search-button {
  min-height: 0;
  flex-shrink: 0;
}

.disabled {
  cursor: default;
}
.disabled.active:focus-within {
  outline: var(--searchfield-focus-outline, var(--focus-light-bg));
}

.search-button-container {
  display: inline-flex;
  align-items: center;
  padding-inline-start: 0.25rem;
}

