// Hover and active states should not apply to pressed or disabled states
.pressed {
  &,
  &:hover,
  &:active {
    color: var(--button-fg);
    background-color: var(--button-bg);
    border-color: var(--button-bd);
  }
}

.toggle.disabled {
  &,
  &:hover,
  &:active {
    color: var(--button-fg);
    background-color: var(--button-bg);
    border-color: var(--button-bd);

    // icon color
    & :global(.iconColor) {
      --icon-default-fg: var(--button-fg);
    }

    & :global(.alert) {
      --icon-alert-fg: var(--button-fg);
    }

    & :global(.warning) {
      --icon-warning-fg: var(--button-fg);
    }

    & :global(.information) {
      --icon-information-fg: var(--button-fg);
    }

    & :global(.success) {
      --icon-success-fg: var(--button-fg);
    }

    & :global(.neutral) {
      --icon-neutral-fg: var(--button-fg);
    }
  }
}

.toggle.disabled:focus {
  outline-color: var(--focus-light-bg-color);
}

.toggle.variant:active:not(.disabled) {
  &:has(:global(.iconColor)) {
    --icon-default-override-fg: var(--button-fg);
  }
  &:has(:global(.alert)) {
    --icon-alert-override-fg: var(--toggle-variant-fg-alert);
    color: var(--toggle-variant-fg-alert);
    background-color: var(--toggle-variant-bg-alert);
    border-color: var(--toggle-variant-bd-alert);
  }
  &:has(:global(.warning)) {
    --icon-warning-override-fg: var(--toggle-variant-fg-warning);
    color: var(--toggle-variant-fg-warning);
    background-color: var(--toggle-variant-bg-warning);
    border-color: var(--toggle-variant-bd-warning);
  }
  &:has(:global(.information)) {
    --icon-information-override-fg: var(--toggle-variant-fg-info);
    color: var(--toggle-variant-fg-info);
    background-color: var(--toggle-variant-bg-info);
    border-color: var(--toggle-variant-bd-info);
  }
  &:has(:global(.success)) {
    --icon-success-override-fg: var(--toggle-variant-fg-success);
    color: var(--toggle-variant-fg-success);
    background-color: var(--toggle-variant-bg-success);
    border-color: var(--toggle-variant-bd-success);
  }
  &:has(:global(.neutral)) {
    --icon-neutral-override-fg: var(--toggle-variant-fg-neutral);
    color: var(--toggle-variant-fg-neutral);
    background-color: var(--toggle-variant-bg-neutral);
    border-color: var(--toggle-variant-bd-neutral);
  }
}
