@use 'animation';

// ** Mixin for animated focus border on input fields ** //

// Focus border default style
@mixin AnimFocusBorder($tokenPrefix) {
  position: absolute;
  content: '';
  block-size: var(--#{$tokenPrefix}-bottom-bd-block-size, 1px);
  background-color: var(--#{$tokenPrefix}-active-bd, var(--#{$tokenPrefix}-focus-bd));
  inline-size: 0;
  inset-block-end: var(--#{$tokenPrefix}-bottom-bd-inset-block-end, 0);
  inset-inline-start: 0;
  transition: all animation.duration(var(--#{$tokenPrefix}-bottom-bd-anim-dur))
    var(--#{$tokenPrefix}-bottom-bd-anim-tm-ease-out);
}

// Focused
@mixin AnimFocusBorderActive($tokenPrefix) {
  inline-size: 100%;
  transition: all animation.duration(var(--#{$tokenPrefix}-bottom-bd-anim-dur))
    var(--#{$tokenPrefix}-bottom-bd-anim-tm-ease-in);
}

// Focused alert border
@mixin AnimFocusBorderActiveAlert($tokenPrefix) {
  background-color: var(--#{$tokenPrefix}-error-bd, var(--#{$tokenPrefix}-alert-bd));
}

// Focused warning border
@mixin AnimFocusBorderActiveWarning($tokenPrefix) {
  background-color: var(--#{$tokenPrefix}-warning-bd);
}

// Focused alert/warning status border (e.g. select)
@mixin AnimFocusBorderActiveStatus($tokenPrefix) {
  background-color: var(--#{$tokenPrefix}-bd);
}
