@use 'utils/animation';
$tree-exp-col-icon-anim-duration-200: animation.duration(
  var(--tree-node-anim-tree-exp-col-icon-dur-200)
);
$tree-arrow-expand-prop: $tree-exp-col-icon-anim-duration-200 var(--tree-node-anim-tf-in) forwards;
$tree-arrow-collapse-prop: $tree-exp-col-icon-anim-duration-200 var(--tree-node-anim-tf-in) forwards;

%node-icon {
  flex-shrink: 0;
}

.item {
  max-width: 100%;
  position: relative;

  .item-inner {
    display: flex;
    align-items: center;
    padding: var(--tree-spacing-xs) 0;
    padding-inline-end: var(--tree-spacing-s);
    gap: var(--tree-spacing-xs);
    box-sizing: border-box;

    > .node-label,
    > .node-icon,
    > .expand-icon {
      min-width: 0;
      z-index: 0;
    }

    > .node-label {
      align-items: baseline;
      font-size: var(--tree-font-size-m);
      user-select: none;
    }
  }

  .node-icon {
    @extend %node-icon;
  }

  .node-status-icon {
    @extend %node-icon;
    margin-inline-start: auto;
  }

  .expand-icon {
    @extend %node-icon;
    margin-block-start: var(--tree-spacing-xxs);
  }

  .expand-icon-pointer-cursor {
    cursor: pointer;
  }
}

.item:not(.selected):hover .item-inner {
  background-color: var(--tree-node-hover-bg);
  cursor: var(--tree-node-hover-cursor);
}

.item:focus {
  &::after {
    content: '';
    position: absolute;
    inset: 0;
    outline: var(--focus-light-bg);
    outline-offset: calc(-1 * var(--focus-width));
    pointer-events: none;
  }

  &.selected {
    &::after {
      outline: var(--tree-node-selected-focus-bg);
      outline-offset: calc(-1 * var(--focus-width) - 1px);
    }
  }
}

.selected .item-inner {
  background-color: var(--tree-selected-inactive-bg);
  filter: var(--tree-selected-inactive-filter);
  cursor: var(--tree-node-hover-cursor);
  position: relative;
  &:after {
    position: absolute;
    content: '';
    height: 100%;
    width: var(--tree-node-selected-indicator-width);
    inset-block-start: 0;
    inset-inline-start: 0;
    background: var(--tree-node-selected-indicator-bd);
  }
}

:focus-within .selected .item-inner {
  background-color: var(--tree-selected-bg);
  filter: var(--tree-selected-filter);
}

.show-scroll label {
  white-space: pre;
}

.icon-expand-animation {
  :global {
    animation: nova-global-treearrowExpand $tree-arrow-expand-prop;
  }
}

.icon-collapse-animation {
  :global {
    animation: nova-global-treearrow-collapse $tree-arrow-collapse-prop;
  }
}
.disabled-node.item-inner {
  position: relative;
  &::after {
    position: absolute;
    content: '';
    height: 100%;
    width: var(--tree-node-disabled-border-size);
    inset-block-start: 0;
    inset-inline-start: 0;
    background: var(--tree-node-disabled-border-color);
  }
}
