.content-container {
  align-items: center;
  flex-grow: 1;
  min-width: 0;
  position: relative; // this will resolve selection text hidden issue in mac safari https://bugs.webkit.org/show_bug.cgi?id=200251
  max-width: 100%;

  &.read {
    // Align read/unread list items
    margin-inline-start: calc(0.875rem + var(--list-spacing-xs));
  }
}

.item-icon {
  flex-shrink: 0;
}

.link-wrapper {
  display: flex;
  min-width: 0;
}

.unread {
  font-weight: var(--list-item-font-weight-thick);
}

.dot {
  align-self: flex-start;
  margin: var(--list-spacing-xxs) var(--list-spacing-xs) var(--list-spacing-xxs) 0;
  flex-shrink: 0;
}

.link {
  max-width: 100%;

  &.disabled {
    color: var(--list-item-disabled-fg);
  }

  &.bold {
    font-weight: var(--list-item-font-weight-thick);

    .read & {
      font-weight: var(--list-item-font-weight-default);
    }
  }
}

.item-description-text {
  min-width: 1.5rem;

  &.disabled {
    color: var(--list-item-disabled-fg);
  }

  &.unread {
    font-weight: var(--list-item-font-weight-thick);
  }

  font-size: var(--list-item-font-size-s);
  color: var(--list-item-description-fg);
}

%content-box {
  box-sizing: border-box;
  display: flex;
  min-height: 0.875rem;
  color: var(--list-item-fg);
  flex-grow: 1;
  min-width: 0;
}

.single-line {
  @extend %content-box;
  flex-direction: row;
  align-items: center;
  gap: var(--list-item-gap);
}

.double-lines {
  @extend %content-box;
  flex-direction: column;
  align-items: flex-start;
}

.sub-section {
  display: flex;
  align-items: center;
  font-size: var(--list-item-font-size-s);
  min-width: 0;
  max-width: 100%;
}
