.alert, .alert:hover {
  border-color: var(--date-picker-error-bd);
  background: var(--date-picker-error-bg);
}

.warning, .warning:hover {
  border-color: var(--date-picker-warning-bd);
  background: var(--date-picker-warning-bg);
}

.input {
  flex-grow: 1;
}

.input-cursor {
  caret-color: transparent;
}

.icon-button {
  overflow: visible;
}

.editable-icon-button {
  border-inline-start: 0;
}

.footer {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 0.5rem;
  justify-content: flex-end;
}

.form {
  background-color: var(--datefield-bg);
  border-color: var(--datefield-bd);
}

.input-enabled > input {
  color: var(--datefield-fg);
}
.input-enabled > input:focus {
  border-width: 0;
  outline: none;
}

.input-error > input {
  border-width: 0;
  color: var(--datefield-active-fg);
  outline: none;
}

.form {
  display: inline-flex;
  border-width: 1px;
  border-right: 1px solid var(--datefield-noneditable-bg);
  width: 100%;
}
.form.active {
  border-style: solid;
  outline: var(--datefield-focus-outline);
  outline-offset: var(--datefield-focus-outline-offset);
}
.form.active:not(.disabled) {
  border-color: var(--datefield-active-bd);
  background: var(--date-picker-active-bg);
}
.form.error {
  border-color: var(--datefield-error-bd);
  background-color: var(--datefield-error-bg);
}
.form.warning {
  border-color: var(--datefield-warning-bd);
  background-color: var(--datefield-warning-bg);
}

.disabled {
  background-color: var(--datefield-disabled-bg);
  border-color: var(--datefield-disabled-bd);
  color: var(--datefield-disabled-fg);
  cursor: default;
}

.date-input {
  flex-grow: 1;
}
.date-input > input {
  width: 100%;
  min-height: 1.25rem;
}

.message, .warning-message, .alert-message {
  padding: var(--datefield-message-pd-squish-s);
  font-size: var(--datefield-message-font-size-xs);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  display: block;
}

.alert-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-alert-bd);
  color: var(--datefield-message-alert-fg);
}

.warning-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-warning-bd);
  color: var(--datefield-message-warning-fg);
}

.date-time-container {
  justify-content: center;
  align-items: center;
}

.date-time-content {
  min-height: 18.5rem;
  min-width: 21.5rem;
}
.date-time-content.today-button {
  min-height: 21.2rem;
}
.date-time-content.millisecond {
  min-width: 25rem;
  min-height: 21.2rem;
}

.date-wrapper {
  display: contents;
}

.select {
  min-width: 10rem;
}

