.container {
  display: inline-flex;
  border-width: var(--composite-input-bd-width);
  border-style: solid;
  border-color: var(--composite-input-bd);
  box-sizing: border-box;
  min-height: 1.75rem;
  background: var(--composite-input-bg);
  position: relative;
}
.container:not(.disabled):after {
  position: absolute;
  content: "";
  block-size: var(--composite-input-bottom-bd-block-size, 1px);
  background-color: var(--composite-input-active-bd, var(--composite-input-focus-bd));
  inline-size: 0;
  inset-block-end: var(--composite-input-bottom-bd-inset-block-end, 0);
  inset-inline-start: 0;
  transition: all calc(var(--composite-input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--composite-input-bottom-bd-anim-tm-ease-out);
}
.container:focus-within:after {
  inline-size: 100%;
  transition: all calc(var(--composite-input-bottom-bd-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--composite-input-bottom-bd-anim-tm-ease-in);
}
.container.alert:after {
  background-color: var(--composite-input-error-bd, var(--composite-input-alert-bd));
}
.container.warning:after {
  background-color: var(--composite-input-warning-bd);
}
.container:hover:not(:focus-within) {
  border-color: var(--composite-input-hover-bd);
  background: var(--composite-input-hover-bg);
}
.container.disabled {
  border-color: var(--composite-input-bd);
}
.container.disabled:hover, .container.disabled:focus-within {
  border-color: var(--composite-input-bd);
}
.container:focus-within {
  border-color: var(--composite-input-active-bd);
  background-color: var(--composite-input-active-bg);
}
.container.alert, .container.alert:hover {
  border-color: var(--composite-input-error-bd);
  background-color: var(--composite-input-error-bg);
}
.container.warning, .container.warning:hover {
  border-color: var(--composite-input-warning-bd);
  background-color: var(--composite-input-warning-bg);
}

.input {
  min-height: 0;
  flex-grow: 1;
  flex-basis: min-content;
}

.readonly-button {
  visibility: hidden;
}

.button {
  min-height: 0;
  border: 0;
  overflow: hidden;
}

.editable-button {
  border-start-start-radius: 0;
  border-end-start-radius: 0;
  border-inline-start: 0;
}

