.container {
  display: flex;
  cursor: pointer;
  width: fit-content;
}

.container:focus-within {
  outline: var(--button-focus-outline);
  outline-offset: var(--button-focus-outline-offset);
}

.button {
  border-inline-end-width: 0;
  flex-grow: 1;
  min-width: 0;

  // TODO: we should update this to use the logical border radius when our matrix supports it.
  :root[dir='ltr'] & {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  :root[dir='rtl'] & {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  &:focus-visible {
    outline: none;
    &:focus-visible:active {
      outline: none;
    }
  }
}

.menu {
  padding: var(--split-button-menu-padding);
  border-inline-start-color: var(--split-button-divider-bd, var(--button-bd));

  // TODO: we should update this to use the logical border radius when our matrix supports it.
  :root[dir='ltr'] & {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  :root[dir='rtl'] & {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
}

.button.active,
.container .menu.active {
  color: var(--button-active-fg);
  background-color: var(--button-active-bg);
  //fall back to transparent. The legacy tokens do not define border-color for light weight buttons
  border-color: var(--button-active-bd, transparent);
  filter: var(--button-active-filter);

  :global(.iconColor) {
    --icon-default-fg: var(--button-active-fg);
  }
}

.menu:hover,
.button:hover + .menu {
  border-inline-start-color: var(--split-button-divider-bd, var(--button-hover-bd));
}

.menu.active,
.button:active + .menu,
.button.active + .menu {
  border-inline-start-color: var(--split-button-divider-bd, var(--button-active-bd));
}
