%stack-button {
  display: flex;
  overflow: hidden;
  max-width: 25rem;
}
.stack-button_top,
.stack-button_bottom {
  @extend %stack-button;
  white-space: nowrap;
  text-overflow: clip;
  flex-shrink: 0;
}
.stack-button_left,
.stack-button_right {
  @extend %stack-button;
  height: auto;
  white-space: normal;
  text-overflow: ellipsis;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  display: block;
  width: 100%;
}

.stretch {
  // Some button types require consistent cross-axis length.
  // For example, vertical tabs should have all the same width.
  align-self: stretch;
}

.tab {
  // Gets vertical tabs to fill parent width. This is only needed
  // because Tab defaults to display: inline-flex.
  // In nova-commons, TabBar sets display: flex on vertical tabs.
  width: 100%;
}
.align-self-start {
  align-self: flex-start;
}

.align-self-center {
  align-self: center;
}