.container_horizontal, .container_vertical {
  display: flex;
}

.container_vertical {
  flex-direction: column;
  min-height: var(--splitter-min-size);
}

.container_horizontal {
  flex-direction: row;
  min-width: var(--splitter-min-size);
  min-height: 0;
  overflow: hidden;
}

.container_horizontal:has(.section:last-child.collapsed) {
  justify-content: flex-end;
}

.section {
  overflow: auto;
  box-sizing: border-box;
}
.container_horizontal .section.hasPadding {
  padding: 0 var(--splitter-section-padding);
}
.container_vertical .section.hasPadding {
  padding: var(--splitter-section-padding) 0;
}
.section.collapsed {
  visibility: hidden;
}

.bar_vertical, .bar_horizontal {
  background-color: var(--splitter-bg);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.bar_vertical:focus::after, .bar_horizontal:focus::after {
  content: "";
  position: absolute;
  inset: 0;
  outline: var(--focus-light-bg);
  outline-offset: var(--splitter-outline-offset);
}

.bar_vertical::before, .bar_horizontal::before {
  content: "";
  background: transparent;
  position: absolute;
  z-index: 1;
}

.collapse-icon_vertical::before, .collapse-icon_horizontal::before, .resize-icon::before {
  content: "";
  position: absolute;
  background: transparent;
  height: var(--splitter-icon-target-area-size);
  width: var(--splitter-icon-target-area-size);
  inset-inline-start: 50%;
  inset-block-start: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.bar_horizontal {
  width: var(--splitter-bar-size);
  cursor: col-resize;
  flex-direction: column;
  align-items: center;
}
.bar_horizontal::before {
  inset-block-start: 0;
  inset-block-end: 0;
}
.bar_horizontal:hover::before {
  width: 1.5rem;
}
.bar_horizontal:focus::after {
  inset-block-start: calc(var(--focus-width) + 2px);
  inset-block-end: calc(var(--focus-width) + 2px);
}

.bar_vertical {
  height: var(--splitter-bar-size);
  cursor: row-resize;
}
.bar_vertical::before {
  inset-inline-start: 0;
  inset-inline-end: 0;
}
.bar_vertical:hover::before {
  height: 1.5rem;
}
.bar_vertical:focus::after {
  inset-inline-start: calc(var(--focus-width) + 2px);
  inset-inline-end: calc(var(--focus-width) + 2px);
}

.hide-splitter-bar {
  background-color: transparent;
}

.resize-icon {
  pointer-events: none;
  position: absolute;
}
.collapse-icon_horizontal {
  cursor: pointer;
  z-index: 1;
  position: absolute;
}
.resize-icon + .collapse-icon_horizontal {
  inset-block-start: calc(50% + 1.5rem);
}
.collapse-icon_vertical {
  height: var(--splitter-icon-size);
  cursor: pointer;
  z-index: 1;
  position: absolute;
}
.resize-icon + .collapse-icon_vertical {
  inset-inline-start: calc(50% + 1.5rem);
}
.resizable {
  cursor: default;
}

