@use 'reactDnd/dndstyles';

.draggable {
  @extend %draggable;
}

.dragged-item {
  @extend %draggedItem;
}

.drag-image-preview-wrapper {
  @include dndstyles.dragImagePreviewWrapper('dnd-tree-node');
  padding: 0 var(--dnd-tree-node-spacing-xs); // added for better visual appearance, so we should be good not using spacing tokens
  box-sizing: border-box;
  position: relative;
}

// Use before/after stack position to the drag-count-stack-two / drag-count-stack-three as per the drag image max stack 3 images.
@include dndstyles.dragImagePreviewStack('drag-count-stack-two', ('before'));
@include dndstyles.dragImagePreviewStack('drag-count-stack-three', ('before', 'after'));

// Drop zone
.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}

.node-count {
  position: absolute;
  inset-block-start: -0.75rem;
  inset-inline-end: -0.93rem;

  //NOTE: Use div here to make Badge round because we don't have direct contorl on div.square
  div {
    border-radius: 50%;
  }
}
