@use 'utils/input';

.container {
  display: inline-flex;
  border-width: var(--composite-input-bd-width);
  border-style: solid;
  border-color: var(--composite-input-bd);
  box-sizing: border-box;
  min-height: 1.75rem;
  background: var(--composite-input-bg);
  position: relative;

  &:not(.disabled):after {
    @include input.AnimFocusBorder($tokenPrefix: 'composite-input');
  }

  //  animation border
  &:focus-within:after {
    @include input.AnimFocusBorderActive($tokenPrefix: 'composite-input');
  }

  &.alert:after {
    @include input.AnimFocusBorderActiveAlert($tokenPrefix: 'composite-input');
  }

  &.warning:after {
    @include input.AnimFocusBorderActiveWarning($tokenPrefix: 'composite-input');
  }

  &:hover:not(:focus-within) {
    border-color: var(--composite-input-hover-bd);
    background: var(--composite-input-hover-bg);
  }

  &.disabled {
    border-color: var(--composite-input-bd);

    &:hover,
    &:focus-within {
      border-color: var(--composite-input-bd);
    }
  }

  // Gives the blue border to the entire container when focused
  &:focus-within {
    border-color: var(--composite-input-active-bd);
    background-color: var(--composite-input-active-bg);
  }

  &.alert,
  &.alert:hover {
    border-color: var(--composite-input-error-bd);
    background-color: var(--composite-input-error-bg);
  }

  &.warning,
  &.warning:hover {
    border-color: var(--composite-input-warning-bd);
    background-color: var(--composite-input-warning-bg);
  }
}

.input {
  min-height: 0;
  flex-grow: 1;
  flex-basis: min-content;
}

.readonly-button {
  visibility: hidden;
}

.button {
  min-height: 0;
  border: 0;
  overflow: hidden;
}

.editable-button {
  border-start-start-radius: 0;
  border-end-start-radius: 0;
  border-inline-start: 0;
}
