.inner {
  height: 100%;
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  flex-direction: column;
  box-sizing: border-box;
  position: relative;
  max-height: 100%;
  min-height: 100%;
  border: solid 1px;
  padding-block-end: 1rem;
}

.content {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.grip-icon-wrapper {
  cursor: se-resize;
  inline-size: 1rem;
  block-size: 1rem;
  position: absolute;
  inset-inline-end: 0;
  inset-block-end: 0;
  line-height: 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;

  &:hover,
  &:focus {
    inline-size: 1.5rem;
    block-size: 1.5rem;
  }

  & :global(.iconColor) {
    --icon-default-override-fg: var(--resizer-color-text-label);
  }

  &:focus {
    outline: var(--focus-light-bg);
    outline-offset: var(--resizer-focus-outline);
  }
}

.grip-cursor-rtl {
  cursor: sw-resize;
}
