.content-container {
  align-items: center;
  flex-grow: 1;
  min-width: 0;
  position: relative;
  max-width: 100%;
}
.content-container.read {
  margin-inline-start: calc(0.875rem + var(--list-spacing-xs));
}

.item-icon {
  flex-shrink: 0;
}

.link-wrapper {
  display: flex;
  min-width: 0;
}

.unread {
  font-weight: var(--list-item-font-weight-thick);
}

.dot {
  align-self: flex-start;
  margin: var(--list-spacing-xxs) var(--list-spacing-xs) var(--list-spacing-xxs) 0;
  flex-shrink: 0;
}

.link {
  max-width: 100%;
}
.link.disabled {
  color: var(--list-item-disabled-fg);
}
.link.bold {
  font-weight: var(--list-item-font-weight-thick);
}
.read .link.bold {
  font-weight: var(--list-item-font-weight-default);
}

.item-description-text {
  min-width: 1.5rem;
  font-size: var(--list-item-font-size-s);
  color: var(--list-item-description-fg);
}
.item-description-text.disabled {
  color: var(--list-item-disabled-fg);
}
.item-description-text.unread {
  font-weight: var(--list-item-font-weight-thick);
}

.double-lines, .single-line {
  box-sizing: border-box;
  display: flex;
  min-height: 0.875rem;
  color: var(--list-item-fg);
  flex-grow: 1;
  min-width: 0;
}

.single-line {
  flex-direction: row;
  align-items: center;
  gap: var(--list-item-gap);
}

.double-lines {
  flex-direction: column;
  align-items: flex-start;
}

.sub-section {
  display: flex;
  align-items: center;
  font-size: var(--list-item-font-size-s);
  min-width: 0;
  max-width: 100%;
}

